.chart {
  flex: 4;
  -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  border-radius: 10px;
  padding: 10px;
  .title {
    text-align: center;
    font-weight: bold;
  }
}

@media (max-width: 575.98px) {
  .chart {
    flex: 1;
    .title {
      font-size: 16px;
      padding: 10px 0px;
    }
  }
}
